import React, {useContext, useState} from 'react';

import Modal from '../UI/Modal';
import classes from './Cart.module.css';
import CartContext from '../../store/cart-context';
import CartItem from './CartItem';
import Checkout from './Checkout';
import { db } from '../Meals/MealItem/firebase'
import Cards from 'react-credit-cards';


const Cart = props => {
	const [isCheckout, setIsCheckout] = useState(false);
	const [isSubmitting, setIsSubmitting] = useState(false);
	const [didSubmit, setDidSubmit] = useState(false);
	const cartCtx = useContext(CartContext);

	const totalAmount = `Ksh${cartCtx.totalAmount.toFixed(2)}`;
	const hasItems = cartCtx.items.length > 0;

	const cartItemRemoveHandler = id => {
		cartCtx.removeItem(id);
	};

	const cartItemAddHandler = item => {
		cartCtx.addItem({...item, amount: 1});
	};

	const orderHandler = () => {
		setIsCheckout(true);
	};
  console.log("Items: ",cartCtx.items)
	const submitOrderHandler = async (userData) => {
		setIsSubmitting(true);
        console.log("User: ",userData.name)

		db.child("orders").push({
			name : userData.name,
			street: userData.street,
			city: userData.city,
			// postalCode: userData.enteredPostalCode,
			timestamp:Date.now(),
			totalAmount,
			orderedItems: cartCtx.items
		  }).catch(alert);
		// await fetch('https://react-http-4e5dc-default-rtdb.firebaseio.com/orders.json', {
		// 	method: 'POST',
		// 	body: JSON.stringify({
		// 		user: userData,
		// 		orderedItems: cartCtx.items
		// 	})
		// });
		        // const data = {
		// 	name: userData.enteredName,
		// 	street: userData.enteredStreet,
		// 	city: userData.enteredCity,
		// 	address: userData.enteredAdress,
		// 	orderedItems: cartCtx.items
		// }

		
		// db.child("orders").push(data, (err) => {
		// 	if(err){
		// 		toast.error(err)
		// 	}else{
		// 		toast.success("Menu been added successfully")
		// 	}
		// });
		setIsSubmitting(false);
		setDidSubmit(true);
		cartCtx.clearCart();
	};

	const cartItems = (<ul className = {classes['cart-items']}>
		{cartCtx.items.map(item => (
			<CartItem 
				key = {item.id}
				name = {item.name} 
				amount = {item.amount} 
				price = {item.price} 
				image = {item.image}
				onRemove = {cartItemRemoveHandler.bind(null, item.id)} 
				onAdd = {cartItemAddHandler.bind(null, item)} 
			/>
		))}
	</ul>);

	const modalActions = (
		<div className = {classes.actions}>
			<button className = {classes['button--alt']} onClick = {props.onCloseCart}>Close</button>
			{hasItems && <button className = {classes.button} onClick = {orderHandler}>Order</button>}
		</div>
	);

	const cartModalContent = (
		<React.Fragment>
			{cartItems}
			<div className = {classes.total}>
				<span style={{color: "#cc5500"}}>Total Amount</span>
				<span style={{color: "#cc5500"}}>{totalAmount}</span>
			</div>
			{isCheckout && <Checkout onConfirm = {submitOrderHandler} onCancel = {props.onCloseCart} />}
			{!isCheckout && modalActions}
		</React.Fragment>
	);

	const isSubmittingModalContent = <p>Sending order data...</p>; 
	const didSubmitModalContent = (
		<React.Fragment>
			<p>Successfully sent the order!</p>
			<div className = {classes.actions}>
				<button className = {classes.button} onClick = {props.onCloseCart}>Close</button>
			</div>		
		</React.Fragment>
	);	

	return (
		<Modal onCloseCart = {props.onCloseCart}>
			{!isSubmitting && !didSubmit && cartModalContent}
			{isSubmitting && isSubmittingModalContent}
			{!isSubmitting && didSubmit && didSubmitModalContent}
		</Modal>
	);
};

export default Cart;